import React from 'react'

const Contact = () => {
  function check() {
    alert("お問い合わせ内容が送信されました。");
  }
  return (
    <div>
      <br /><br />
      <p>メールアドレス</p>
      <input type="text" maxLength={255} />
      <p>お名前</p>
      <input type="text" maxLength={255} />
      <p>電話番号</p>
      <input type="text" maxLength={255} />
      <p>お問い合わせ内容</p>
      <input type="text" maxLength={255} id="input-message" />
      <br />
      <p id="output-message"></p>
      <input type="submit" value="送信する" onClick={showMessage} />

    </div >
  )
}


const showMessage = () => {
  const textbox = document.getElementById("input-message");
  const inputValue = textbox.value;

  if (textbox.value === 'KNKTSK') {
    //テキストボックスの値を使って、出力するメッセージを生成する
    const output = "よくぞ暗証番号を打ち破った。ようこそ、<a href='/toshiki-profile/Tenten'>てんてんワールド</a>";
    //出力用のp要素にメッセージを表示
    document.getElementById("output-message").innerHTML = output;
  } else {
    //テキストボックスの値を使って、出力するメッセージを生成する
    const output = "入力された内容は「" + inputValue + "」です。";
    //出力用のp要素にメッセージを表示
    document.getElementById("output-message").innerHTML = output;
  }


}

export default Contact
