import React from 'react'
import {
  BrowserRouter,
  Routes,
  Route
} from 'react-router-dom'

import Navibar from './navibar'
import First_view from './First-view'
import Favorite from './Favorite'
import Contact from './Contact'
import Archive from './Archive'
import About from './About'
import Button_ten from './button-ten'


const App = () => {
  return (
    <>
      <Navibar />
      <BrowserRouter>
        <Routes basename={process.env.PUBLIC_URL}>
          <Route path='/toshiki-profile/' element={<First_view />} />
          <Route path='/toshiki-profile/About' element={<About />} />
          <Route path='/toshiki-profile/Archive' element={<Archive />} />
          <Route path='/toshiki-profile/Service' element={<Favorite />} />
          <Route path='/toshiki-profile/Contact' element={<Contact />} />
          <Route path='/toshiki-profile/Tenten' element={<Button_ten />} />
        </Routes>
      </BrowserRouter>
    </>
  )
}

export default App;
